'use client';

import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Sparkles, Gamepad2, ArrowRight, Mail, Lock, User, Facebook, AlertCircle } from 'lucide-react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';

export default function LoginPage() {
  const { user, loading, signInWithGoogle, signInWithFacebook, loginWithEmail, registerWithEmail } = useAuth();
  const router = useRouter();

  // Mode: LOGIN or REGISTER
  const [isLogin, setIsLogin] = useState(true);

  // Form State
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [displayName, setDisplayName] = useState('');
  const [formLoading, setFormLoading] = useState(false);
  const [errorMsg, setErrorMsg] = useState('');

  // Auto Redirect
  useEffect(() => {
    if (user && !loading) router.push('/dashboard');
  }, [user, loading, router]);

  // Handlers
  const handleEmailAuth = async (e: React.FormEvent) => {
    e.preventDefault();
    setErrorMsg('');
    setFormLoading(true);

    try {
      if (isLogin) {
        await loginWithEmail(email, password);
      } else {
        if (!displayName) throw new Error("กรุณากรอกชื่อผู้ใช้งาน (Display Name)");
        await registerWithEmail(email, password, displayName);
      }
      // Success is handled by auth listener -> redirect
    } catch (err: any) {
      // Human readable errors
      let msg = err.message;
      if (msg.includes("auth/email-already-in-use")) msg = "อีเมลนี้ถูกใช้งานแล้ว";
      if (msg.includes("auth/wrong-password")) msg = "รหัสผ่านไม่ถูกต้อง";
      if (msg.includes("auth/user-not-found")) msg = "ไม่พบผู้ใช้งานนี้";
      if (msg.includes("auth/weak-password")) msg = "รหัสผ่านต้องมีความยาวอย่างน้อย 6 ตัวอักษร";
      setErrorMsg(msg);
      setFormLoading(false);
    }
  };

  const socialLogin = async (provider: 'google' | 'facebook') => {
    try {
      setErrorMsg('');
      if (provider === 'google') await signInWithGoogle();
      if (provider === 'facebook') await signInWithFacebook();
    } catch (err: any) {
      setErrorMsg(err.message);
    }
  };

  return (
    <div className="min-h-screen bg-[#0f172a] flex flex-col items-center justify-center p-6 relative overflow-hidden font-poppins">

      {/* Background FX (Mystic Theme) */}
      <div className="absolute top-0 left-0 w-full h-full bg-[radial-gradient(ellipse_at_top,_var(--tw-gradient-stops))] from-blue-900/20 via-[#0f172a] to-[#0f172a]" />
      <div className="absolute top-10 left-10 w-32 h-32 bg-purple-500/20 rounded-full blur-[80px] animate-pulse"></div>
      <div className="absolute bottom-10 right-10 w-40 h-40 bg-blue-500/20 rounded-full blur-[80px]"></div>

      {/* Main Container */}
      <motion.div
        initial={{ opacity: 0, scale: 0.95, y: 30 }}
        animate={{ opacity: 1, scale: 1, y: 0 }}
        transition={{ duration: 0.5, type: "spring" }}
        className="w-full max-w-md bg-[#1e293b]/90 backdrop-blur-2xl border border-white/10 shadow-2xl rounded-3xl overflow-hidden relative z-10"
      >
        {/* Header Section */}
        <div className="p-8 pb-0 text-center">
          <div className="w-16 h-16 bg-gradient-to-br from-blue-600 to-indigo-600 rounded-2xl mx-auto flex items-center justify-center shadow-lg shadow-blue-500/40 mb-4 transform -rotate-3 hover:rotate-0 transition-transform duration-300">
            <Gamepad2 className="w-8 h-8 text-white" />
          </div>
          <h1 className="text-2xl font-bold text-white tracking-tight mb-1">Welcome to Guild</h1>
          <p className="text-slate-400 text-sm">Sign in to manage your adventure</p>
        </div>

        {/* Toggle Login/Register */}
        <div className="px-8 mt-6">
          <div className="grid grid-cols-2 p-1 bg-[#0f172a]/50 rounded-xl">
            <button
              onClick={() => setIsLogin(true)}
              className={`py-2 text-sm font-bold rounded-lg transition-all ${isLogin ? 'bg-blue-600 text-white shadow-lg' : 'text-slate-500 hover:text-slate-300'}`}
            >
              Login
            </button>
            <button
              onClick={() => setIsLogin(false)}
              className={`py-2 text-sm font-bold rounded-lg transition-all ${!isLogin ? 'bg-blue-600 text-white shadow-lg' : 'text-slate-500 hover:text-slate-300'}`}
            >
              Sign Up
            </button>
          </div>
        </div>

        {/* Form Section */}
        <div className="p-8 pt-6">

          {/* Social Logins */}
          <div className="grid grid-cols-2 gap-3 mb-6">
            <button
              onClick={() => socialLogin('google')}
              className="flex items-center justify-center gap-2 py-2.5 bg-white text-slate-900 rounded-xl font-bold text-xs hover:bg-gray-100 transition-colors shadow-sm"
            >
              <span className="text-lg text-blue-500 font-black">G</span> Google
            </button>
            <button
              onClick={() => socialLogin('facebook')}
              className="flex items-center justify-center gap-2 py-2.5 bg-[#1877F2] text-white rounded-xl font-bold text-xs hover:bg-[#166fe5] transition-colors shadow-sm"
            >
              <span className="text-lg font-black">f</span> Facebook
            </button>
          </div>

          <div className="relative flex py-2 items-center mb-6">
            <div className="flex-grow border-t border-slate-700/50"></div>
            <span className="flex-shrink-0 mx-4 text-slate-500 text-xs">OR EMAIL</span>
            <div className="flex-grow border-t border-slate-700/50"></div>
          </div>

          <form onSubmit={handleEmailAuth} className="space-y-4">

            {/* Display Name (Register Only) */}
            {!isLogin && (
              <motion.div initial={{ height: 0, opacity: 0 }} animate={{ height: 'auto', opacity: 1 }} className="relative">
                <User className="absolute left-3 top-3.5 text-slate-500 w-5 h-5" />
                <input
                  type="text" required={!isLogin}
                  placeholder="Adventurer Name"
                  value={displayName} onChange={e => setDisplayName(e.target.value)}
                  className="w-full bg-[#0f172a] border border-slate-700 rounded-xl py-3 pl-10 pr-4 text-white text-sm focus:border-blue-500 outline-none transition-all placeholder-slate-600"
                />
              </motion.div>
            )}

            <div className="relative">
              <Mail className="absolute left-3 top-3.5 text-slate-500 w-5 h-5" />
              <input
                type="email" required
                placeholder="Email Address"
                value={email} onChange={e => setEmail(e.target.value)}
                className="w-full bg-[#0f172a] border border-slate-700 rounded-xl py-3 pl-10 pr-4 text-white text-sm focus:border-blue-500 outline-none transition-all placeholder-slate-600"
              />
            </div>

            <div className="relative">
              <Lock className="absolute left-3 top-3.5 text-slate-500 w-5 h-5" />
              <input
                type="password" required
                placeholder="Password"
                value={password} onChange={e => setPassword(e.target.value)}
                className="w-full bg-[#0f172a] border border-slate-700 rounded-xl py-3 pl-10 pr-4 text-white text-sm focus:border-blue-500 outline-none transition-all placeholder-slate-600"
              />
            </div>

            {errorMsg && (
              <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="p-3 bg-red-500/10 border border-red-500/20 rounded-lg flex items-center gap-2 text-xs text-red-300">
                <AlertCircle className="w-4 h-4 shrink-0" /> {errorMsg}
              </motion.div>
            )}

            <button
              type="submit"
              disabled={formLoading}
              className="w-full py-3.5 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-500 hover:to-indigo-500 text-white font-bold rounded-xl shadow-lg shadow-blue-500/25 flex items-center justify-center gap-2 transition-all active:scale-[0.98] mt-2 group"
            >
              {formLoading ? (
                <span className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
              ) : (
                <>
                  {isLogin ? 'Start Adventure' : 'Create Account'}
                  <ArrowRight className="w-4 h-4 group-hover:translate-x-1 transition-transform" />
                </>
              )}
            </button>
          </form>

        </div>

        {/* Footer */}
        <div className="p-4 bg-[#0f172a]/30 text-center border-t border-white/5">
          <button onClick={() => router.push('/dashboard')} className="text-xs text-slate-500 hover:text-white transition-colors underline">
            Guest Access (Demo Mode)
          </button>
        </div>
      </motion.div>
    </div>
  );
}
